import React from "react";
import {
    Card,
    Button,
    Table,
    Modal,
    Form,
    Input,
    Switch,
    message,
    Tree,
    Transfer
} from "antd";
import axios from "../../axios/axios";
import moment from "moment";
import menuConfig from "../../config/menuConfig";

const FormItem = Form.Item;
const TreeNode = Tree.TreeNode;

export default class Permission extends React.Component {
    state = {
        btnDisabled: true,
        isShowCreateModal: false,//
        isShowAuthModal: false, //
        isShowUserAuthModal:false,//
    };
    params = {
        page: 1,
        pageSize: 10
    };

    componentDidMount() {
        this.getList();
    }

    getList = () => {
        axios.getList(this, "/role/list", this.params);
    };

    //
    onRowClick = (item, index) => {
        this.setState({
            selectedRowKeys: [index],
            selectedItem: item,
            btnDisabled: false
        });
    };


    /************************************************************************************************ */
    //
    handleCreate = () => {
        this.setState({
            isShowCreateModal: true
        });
    };

    //-
    onCreateSubmit = () => {
        this.roleForm.props.form.validateFields((err, values) => {
            if (!err) {
                axios
                    .ajax({
                        url: "/role/add",
                        data: values
                    })
                    .then(res => {
                        if (res.code === 0) {
                            message.success("!");
                            this.getList();
                            this.roleForm.props.form.resetFields();
                            this.setState({
                                isShowCreateModal: false
                            });
                        }
                    });
            }
        });
    };

    /************************************************************************************************ */
    //
    handleAuth = () => {
        let item = this.state.selectedItem;
        this.setState({
            isShowAuthModal: true,
            menus: item.menus
        });
    };

    //key
    setMenus = menus => {
        this.setState({
            menus
        });
    };

    //-
    onAuthSubmit = () => {
        this.authForm.props.form.validateFields((err, values) => {
            if (!err) {
                axios
                    .ajax({
                        url: "/role/auth",
                        data: {
                            ...values,
                            menus: this.state.menus
                        }
                    })
                    .then(res => {
                        if (res.code === 0) {
                            message.success("!");
                            this.getList();
                            this.setState({
                                isShowAuthModal: false
                            });
                        }
                    });
            }
        });
    };

    /************************************************************************************************ */
    //
    handleUserAuth = () => {
        let item = this.state.selectedItem;
        this.setState({
            isShowUserAuthModal: true,
        });
        this.getUserAuthList(item);
    }

    //
    getUserAuthList = (item) => {
        axios.ajax({
            url: '/role/user/auth',
            params: {
                userId:item.id
            }
        }).then(res => {
            if (res.code === 0) {
                let data = res.result;
                let mockData = [];
                let targetKeys = [];
                if (data && data.length > 0) {
                    data.map(item => {
                        let dataObj = {
                            key: item.userId,
                            title: item.userName,
                            status:item.status,
                        }
                        if (item.status === 1) {
                            targetKeys.push(dataObj.key)
                        }
                        mockData.push(dataObj);
                    })
                    console.log('mockData',mockData)
                    this.setState({
                        mockData,
                        targetKeys
                    })
                }
                
            }
        })
    }

    //-
    onUserAuthSubmit = () => {
        let data = {
            roleId: this.state.selectedItem.id,
            roleName:this.state.selectedItem.roleName,
            userIds:this.state.targetKeys,
        }
        axios.ajax({
            url:'/role/user/auth/edit',
            data
        }).then(res => {
            if (res.code === 0){
                message.success("!");
                this.getList();
                this.setState({
                    isShowUserAuthModal: false,
                    selectedItem: {}
                });
            }
        })
    }


    render() {
        const columns = [
            {
                title: "ID",
                dataIndex: "id"
            },
            {
                title: "",
                dataIndex: "roleName"
            },
            {
                title: "",
                dataIndex: "createTime"
            },
            {
                title: "",
                dataIndex: "status",
                render: status => {
                    return status === 1 ? "" : "";
                }
            },
            {
                title: "",
                dataIndex: "authorizeTime",
                render: authorizeTime => {
                    return moment(authorizeTime * 1000).format(
                        "YYYY-MM-DD HH:mm:ss"
                    );
                }
            },
            {
                title: "",
                dataIndex: "authorizeUserName"
            }
        ];
        columns.map(item => {
            item.align = "center";
        });

        const selectedRowKeys = this.state.selectedRowKeys;
        const rowSelection = {
            type: "radio",
            selectedRowKeys
        };

        return (
            <div>
                <Card>
                    <Button type="primary" onClick={this.handleCreate}>
                        
                    </Button>
                    <Button
                        type="primary"
                        disabled={this.state.btnDisabled}
                        style={{ margin: "0 10" }}
                        onClick={this.handleAuth}
                    >
                        
                    </Button>
                    <Button
                        type="primary"
                        disabled={this.state.btnDisabled}
                        onClick={this.handleUserAuth}>
                        
                    </Button>
                </Card>
                <div style={{ marginTop: 20 }} className="content-wrap">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                        rowSelection={rowSelection}
                        onRow={(item, index) => {
                            return {
                                onClick: () => {
                                    this.onRowClick(item, index);
                                }
                            };
                        }}
                    />
                </div>


                <Modal
                    title=""
                    width={600}
                    visible={this.state.isShowCreateModal}
                    onCancel={() => {
                        this.roleForm.props.form.resetFields();
                        this.setState({
                            isShowCreateModal: false
                        });
                    }}
                    onOk={this.onCreateSubmit}
                >
                    <RoleForm
                        wrappedComponentRef={sss => (this.roleForm = sss)}
                    />
                </Modal>

                
                <Modal
                    title=""
                    width={600}
                    bodyStyle={{
                        height: 400,
                        overflow:'auto'
                    }}
                    visible={this.state.isShowAuthModal}
                    onCancel={() => {
                        this.authForm.props.form.resetFields();
                        this.setState({
                            isShowAuthModal: false
                        });
                    }}
                    onOk={this.onAuthSubmit}
                >
                    <AuthForm
                        info={this.state.selectedItem}
                        menus={this.state.menus}
                        setMenus={this.setMenus}
                        wrappedComponentRef={sss => (this.authForm = sss)}
                    />
                </Modal>



                <Modal
                    title=""
                    width={800}
                    visible={this.state.isShowUserAuthModal}
                    onCancel={() => {
                        this.userAuthForm.props.form.resetFields();
                        this.setState({
                            isShowUserAuthModal: false
                        });
                    }}
                    onOk={this.onUserAuthSubmit}
                >
                    <UserAuthForm
                        info={this.state.selectedItem}
                        mockData={this.state.mockData}
                        targetKeys={this.state.targetKeys}
                        setTargetKeys={(targetKeys) => {
                            this.setState({targetKeys})
                        }}
                        wrappedComponentRef={sss => (this.userAuthForm = sss)}
                    />
                </Modal>


            </div>
        );
    }
}

class RoleForm extends React.Component {
    render() {
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 19 }
        };
        const { getFieldDecorator } = this.props.form;

        return (
            <Form layout="horizontal">
                <FormItem label="" {...formItemLayout}>
                    {getFieldDecorator("roleName", {
                        rules: [
                            {
                                required: true,
                                message: ""
                            },
                            {
                                min: 5,
                                max: 12,
                                message: "5-12"
                            },
                            {
                                pattern: new RegExp("[\u4e00-\u9fa5]", "g"),
                                message: ""
                            }
                        ]
                    })(<Input placeholder="" />)}
                </FormItem>
                <FormItem label="" {...formItemLayout}>
                    {getFieldDecorator("status", {
                        initialValue: true,
                        valuePropName: "checked"
                    })(
                        <Switch
                            checkedChildren=""
                            unCheckedChildren=""
                        />
                    )}
                </FormItem>
            </Form>
        );
    }
}

RoleForm = Form.create({})(RoleForm);

class AuthForm extends React.Component {
    getTreeNode = data => {
        return data.map(item => {
            if (item.children && item.children.length > 0) {
                return (
                    <TreeNode title={item.title} key={item.key}>
                        {this.getTreeNode(item.children)}
                    </TreeNode>
                );
            }
            return <TreeNode {...item} />;
        });
    };
    render() {
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 19 }
        };
        const { getFieldDecorator } = this.props.form;
        const info = this.props.info;
        const menus = this.props.menus;

        return (
            <Form layout="horizontal">
                <FormItem label="" {...formItemLayout}>
                    {getFieldDecorator("roleName", {
                        initialValue: info.roleName
                    })(<Input placeholder="" disabled />)}
                </FormItem>
                <FormItem label="" {...formItemLayout}>
                    {getFieldDecorator("status", {
                        initialValue: info.status === 1,
                        valuePropName: "checked"
                    })(
                        <Switch
                            checkedChildren=""
                            unCheckedChildren=""
                        />
                    )}
                </FormItem>
                <Tree
                    defaultExpandAll
                    checkable
                    checkedKeys={menus}
                    onCheck={checkedKeys => {
                        this.props.setMenus(checkedKeys);
                    }}
                >
                    <TreeNode title="">
                        {this.getTreeNode(menuConfig)}
                    </TreeNode>
                </Tree>
            </Form>
        );
    }
}
AuthForm = Form.create({})(AuthForm);



class UserAuthForm extends React.Component {

    handleChange = (targetKeys) => {
        this.props.setTargetKeys(targetKeys);
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 19 }
        };
        const { getFieldDecorator } = this.props.form;
        const { info, mockData, targetKeys } = this.props;
        return (
            <Form layout="horizontal">
                <FormItem label="" {...formItemLayout}>
                    {getFieldDecorator("roleName", {
                        initialValue: info.roleName
                    })(<Input placeholder="" disabled />)}
                </FormItem>
                <FormItem label="" {...formItemLayout}>
                    <Transfer
                        dataSource={mockData}
                        targetKeys={targetKeys}
                        showSearch
                        locale={{
                            itemUnit: '',
                            itemsUnit: '',
                            searchPlaceholder: "",
                            notFoundContent:''
                        }}
                        listStyle={{width: 200,height: 400}}
                        titles={['', '']}
                        render={item => item.title}
                        onChange={this.handleChange}
                    />
                </FormItem>
                
            </Form>
        );
    }
}
UserAuthForm = Form.create({})(UserAuthForm);
