import React from 'react';
import { Card,Table } from 'antd';
import FilterForm from '../../components/filterForm/FilterForm';
import axios from '../../axios/axios';
import moment from 'moment';

export default class Salary extends React.Component{
    state = {
    };
    listArr = [];
    params = {
        date:'2017-05'
    }
    search = (postData) => {
        this.params = {
            ...postData,
        }
        let filterData = this.listArr;
        if (postData.city !== '') {
            filterData = filterData.filter((item) => {
                if (item.city && item.city.indexOf(postData.city) > -1) {
                    return item;
                }
            })
        } 
        if (postData.class || postData.lang) {
            filterData = filterData.filter((item) => {
                if (item.class && item.class.indexOf(postData.class) > -1) {
                    return item;
                }
                if (item.class && item.class.indexOf(postData.lang) > -1) {
                    return item;
                }
            })
        } 
        if (postData.class && postData.lang) {
            filterData = filterData.filter((item) => {
                if (item.class && item.class.indexOf(postData.class) > -1 && item.class.indexOf(postData.lang) > -1) {
                    return item;
                }
            })
        } 

        this.setState({
            list:filterData
        })
    }
    onChange = (time,tiemString) => {
        this.params.date = tiemString;
        this.getList();
    }

    getList() {
        axios.ajax({
            url: 'http://jx.1000phone.net/teacher.php/Api/studentJobData_month',
            params:this.params
        }).then(res => {
            this.listArr = res.student.map((item,index)=> {
                item.key = index
                return item;
            });
            this.setState({
                list:this.listArr
            })
        })
    }

    componentDidMount() {
        this.getList();
    }

    render() {

        const filterList = [
            {
                type: 'timeSelectSingle',
                label: '',
                format: 'YYYY-MM',
                placeholder: '',
                startField: 'date',
                isUnTimeStamp:true,
                width: 200,
                showTime: false,
                initialValue: moment('2017-05'),
                onChange:this.onChange
            },
            {
                type: 'select',
                label: '',
                field: 'city',
                placeholder: '',
                width: 135,
                initialValue: '',
                list: ['','','','','','','']
            },
            {
                type: 'select',
                label: '',
                field: 'lang',
                placeholder: '',
                width: 135,
                initialValue: '',
                list: ['','HTML5','Android','JavaEE','IOS','UI','PHP']
            },
            {
                type: 'input',
                label: '',
                field: 'class',
                placeholder: '1610',
                width:200
            }
        ]
        const columns = [
            {
                title: '',
                dataIndex:'class',
            },
            {
                title: '',
                dataIndex: 'student',
                width:80,
                
            },
            {
                title: '',
                dataIndex: 'education',
                width:80,
                
            },
            {
                title: '',
                dataIndex: 'status',
                width:80,
                
            },
            {
                title: '',
                dataIndex:'profession',
            },
            {
                title: '',
                dataIndex: 'salary',
                width:80,
                
            },
            {
                title: '',
                dataIndex: 'city',
                width:80,
            },
            {
                title: '',
                dataIndex: 'company',
                width:200,
            },
            {
                title: '',
                dataIndex: 'date',
                width:200,
            }
        ]
        columns.map(item => {
            item.align = "center";
        })
        
        


        return (
            <div>
                <Card>
                <FilterForm
                    filterList={filterList}
                    search={this.search}
                />
                </Card>
                <div className="content-wrap">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={false}
                    />
                </div>
            </div>
        )
    }
}